import React from "react";
import { Button, IconButton, Switch, TableCell, TableRow } from "@mui/material";
import { useNavigate } from "react-router-dom";
import CustomTable from "../CustomTable";
// Icons
import EditIcon from "@mui/icons-material/Edit";
import GroupIcon from "@mui/icons-material/Group";
const AllStateLocation = ({ data, stateData, bidoff, statusChanged, openModal }) => {
  const tableHead = [
    "Sr.no",
    "State",
    "Location/City Name",
    "Candidates",
    "Bid Status",
    "Action",
    "Declate Result",
  ];
  let navigate = useNavigate();

  const GotoCandidates = (e, item) => {
    navigate("/candidates", { state: item });
  };
  console.log(data);
  return (
    <>
      <CustomTable tablehead={tableHead}>
        {data &&
          data.map((item, index) => (
            <TableRow key={item._id}>
              <TableCell>{index + 1}</TableCell>
              <TableCell>{item?.state_id?.name}</TableCell>
              <TableCell>{item.name}</TableCell>
              <TableCell>
                <IconButton onClick={(e) => GotoCandidates(e, item)}>
                  <GroupIcon fontSize="small" color="error" />
                </IconButton>
              </TableCell>
              <TableCell>
                <Switch
                  checked={item?.bid_status}
                  disabled={item.result_declare_status}
                  onClick={(e) => (item.result_declare_status ? {} : bidoff(e, item))}
                />
              </TableCell>
              <TableCell>
                <IconButton
                  onClick={(e) => statusChanged(e, item)}
                  disabled={item.result_declare_status}
                >
                  <EditIcon
                    fontSize="small"
                    color={item.result_declare_status ? "default" : "primary"}
                  />
                </IconButton>
              </TableCell>
              <TableCell>
                {item.result_declare_status ? (
                  <Button style={{ padding: 0 }} color="success">
                    Results Declared to {item.candidate_winner_id?.name},{" "}
                    {item.candidate_winner_id?.party_name}
                  </Button>
                ) : (
                  <Button variant="outlined" onClick={(e) => openModal(e, item)}>
                    Declare Results
                  </Button>
                )}
              </TableCell>
            </TableRow>
          ))}
      </CustomTable>
    </>
  );
};
export default AllStateLocation;
