import React, { useEffect, useState } from "react";
import {
  Grid,
  Card,
  CardContent,
  Typography,
  TableRow,
  TableCell,
  Chip,
} from "@mui/material";
import { useLocation } from "react-router-dom";
import moment from "moment";
// Core components
import Header from "../../header";
import Loaders from "../../loader";
import Wrapper from "../../wrapper";
import CustomTable from "../../CustomTable";
//api
import DataService from "../../services/requestApi";

const TransactionUser = (props) => {
  const [list, setList] = useState([]);
  const [loading, setLoading] = useState(false);
  const { state } = useLocation();
  useEffect(() => {
    let Mounted = true;
    const GetData = async () => {
      try {
        setLoading(true);
        const res = await DataService.UserTransaction(state._id);
        if (Mounted) {
          setList(res.data.data);
          console.log(res.data.data);
        }
        setLoading(false);
      } catch (e) {
        console.log(e);
      }
    };
    GetData();
    return () => {
      Mounted = false;
      GetData();
    };
  }, [state]);
  const tableHead = ["Date", "Credit", "Debit", "Transaction Mode"];

  console.log(list);
  return (
    <>
      <Header />
      <Loaders open={loading} />
      <Wrapper>
        <Grid container justifyContent="center" alignItems={"center"}>
          <Grid item xl={11} lg={11} xs={12}>
            <Card className="mb3">
              <CardContent>
                <Grid container justifyContent="center" alignItems={"center"}>
                  <Grid item xl={12} lg={12} xs={12}>
                    <Typography variant="h5">
                      Transaction History of {state?.name}
                    </Typography>
                  </Grid>
                </Grid>
              </CardContent>
            </Card>
            <Card className="mb-3">
              <CustomTable tablehead={tableHead}>
                {list &&
                  list.map((item, index) => (
                    <TableRow key={item._id}>
                      <TableCell>
                        {moment(item.created_at).format("ll")}
                      </TableCell>
                      <TableCell>
                        <span className="text-success">
                          {item.mode === "transfer" || item.mode === "winning"
                            ? `+ ${item.coins}`
                            : "+ 0"}
                        </span>
                      </TableCell>
                      <TableCell>
                        <span className="text-warning">
                          {item.mode === "withdraw" || item.mode === "bidding"
                            ? `- ${item.coins}`
                            : "- 0"}
                        </span>
                      </TableCell>

                      <TableCell>
                        <Chip
                          label={
                            <span className="font-weight-bold text-capitalize">
                              {item?.mode}
                            </span>
                          }
                          style={{ backgroundColor: "#464446", color: "#fff" }}
                        />
                      </TableCell>
                    </TableRow>
                  ))}
              </CustomTable>
            </Card>
          </Grid>
        </Grid>
      </Wrapper>
    </>
  );
};
export default TransactionUser;
