import React from "react";
import moment from "moment";
import {
  ListItem,
  ListItemText,
  ListItemAvatar,
  Typography,
} from "@mui/material";
import Avatar from "@mui/material/Avatar";
import PersonOutlineIcon from "@mui/icons-material/PersonOutline";
import AccountBalanceWalletIcon from "@mui/icons-material/AccountBalanceWallet";
const TransactionCard = ({ item }) => {
  return (
    <ListItem>
      <ListItemAvatar>
        <Avatar>
          <PersonOutlineIcon />
        </Avatar>
      </ListItemAvatar>
      <ListItemText
        primary={item.from_id.name}
        secondary={moment(item.created_at).format("ll")}
      />
      <Typography>
        <span
          style={
            item.mode === "transfer"
              ? {
                  color: "#47c44c",
                  display: "block",
                  textTransform: "capitalize",
                }
              : {
                  color: "#ff4545",
                  display: "block",
                  textTransform: "capitalize",
                }
          }
        >
          {item.mode === "transfer"
            ? item.mode + ` to ${item.to_id?.name}`
            : item.mode + ` from your side`}
          <span className="wallet">
            {item.mode === "transfer" ? "+" : "-"} {item.coins}
            <AccountBalanceWalletIcon fontSize="large" />
          </span>
        </span>
      </Typography>
    </ListItem>
  );
};
export default TransactionCard;
