import React from "react";
import { useNavigate } from "react-router-dom";
import { TableRow, TableCell, Button, Switch } from "@mui/material";
import AutoFixHighIcon from "@mui/icons-material/AutoFixHigh";
import EditIcon from "@mui/icons-material/Edit";
import CustomTable from "../CustomTable";

const AllTickets = ({ stateData, data, changebid, deletetickets, ResultDeclare }) => {
  let navigate = useNavigate();
  const tableHead = [
    "Sr No",
    "Party Name",
    "Ticket Name",
    "Yes/No",
    "User Bids",
    "Action",
    "Bid Status",
  ];
  return (
    <>
      <CustomTable tablehead={tableHead}>
        {data &&
          data.map((item, index) => (
            <TableRow key={item._id}>
              <TableCell>{index + 1}</TableCell>
              <TableCell>{item?.party_id?.name}</TableCell>
              <TableCell>{item?.name}</TableCell>
              <TableCell>
                <div className="tktbox">
                  <div className="box">
                    <p>{item.no_seats}</p>
                    <p>{item.no_winning_percent}</p>
                  </div>
                  <div className="box">
                    <p>{item.yes_seats}</p>
                    <p> {item.yes_winning_percent}</p>
                  </div>
                </div>
              </TableCell>
              <TableCell>
                <Button
                  style={{ paddingLeft: 0 }}
                  onClick={() => navigate("/allbid", { state: item._id })}
                >
                  <AutoFixHighIcon fontSize="small" color="error" />
                </Button>
              </TableCell>
              <TableCell>
                <Button
                  disabled={item.result_declare_status}
                  onClick={(e) =>
                    data.result_declare_status
                      ? {}
                      : navigate("/edittickets", {
                          state: {
                            data: item,
                            stateData,
                          },
                        })
                  }
                >
                  <EditIcon
                    fontSize="small"
                    color={item.result_declare_status ? "default" : "primary"}
                  />
                </Button>
              </TableCell>
              <TableCell>
                <Switch
                  disabled={item.result_declare_status}
                  checked={item?.bid_status}
                  onChange={(e) => changebid(e, item)}
                />
              </TableCell>
            </TableRow>
          ))}
      </CustomTable>
    </>
  );
};
export default AllTickets;
