import React, { useState, useEffect } from "react";

// Core components
import Header from "../../header";
import Loaders from "../../loader";
import Wrapper from "../../wrapper";
import { Card, Grid, CardContent, Typography, Button, TextField } from "@mui/material";
import { useNavigate } from "react-router-dom";
//api
import DataService from "../../services/requestApi";
import queryString from "query-string";

const Myaccount = (props) => {
  let navigate = useNavigate();
  const [names, setName] = useState("");
  const [email, setEmail] = useState("");
  const [numbers, setNumber] = useState("");
  const [password, setPassword] = useState("");
  const [loading, setLoading] = useState(false);
  const [mounted, setMounted] = useState(true);
  useEffect(() => {
    const userData = async () => {
      try {
        setLoading(true);
        const res = await DataService.OwnerData();
        if (mounted) {
          setName(res.data.superadmin.name);
          setEmail(res.data.superadmin.email);
          setNumber(res.data.superadmin.phone);
          setLoading(false);
        }
      } catch (e) {
        console.log(e);
      }
    };
    userData();
    return () => {
      userData();
      setMounted(false);
    };
  }, [mounted]);

  const SubmitData = async (e) => {
    e.preventDefault();
    var Data = {
      name: names,
    };
    setLoading(true);
    if (password.length > 0) {
      Data.password = password;
    }
    try {
      const res = await DataService.ChangeProfile(queryString.stringify(Data));
      if (res.data.status_code === 200) {
        navigate("/");
      }
      setLoading(false);
    } catch (e) {
      console.log(e);
    }
  };
  return (
    <>
      <Header />
      <Loaders open={loading} />
      <Wrapper>
        <Grid container justifyContent="center" alignItems={"center"}>
          <Grid item xl={4} lg={4} xs={12}>
            <Card className="mb3">
              <CardContent>
                <Grid container justifyContent="center" alignItems={"center"}>
                  <Grid item xl={12} lg={12} xs={12} className="mb3">
                    <Typography variant="h5">My Account</Typography>
                  </Grid>
                  <Grid item xl={12}>
                    <form className="formbox" onSubmit={SubmitData}>
                      <TextField
                        className="w-100 mb3"
                        id="Name"
                        label="Name"
                        type="text"
                        variant="outlined"
                        value={names}
                        onChange={(e) => setName(e.target.value)}
                      />
                      <TextField
                        className="w-100 mb3"
                        id="email"
                        label="Email"
                        type="email"
                        variant="outlined"
                        InputProps={{
                          readOnly: true,
                        }}
                        value={email}
                      />
                      <TextField
                        className="w-100 mb3"
                        id="phone"
                        label="Phone Number"
                        type="number"
                        variant="outlined"
                        InputProps={{
                          readOnly: true,
                        }}
                        value={numbers}
                      />
                      <TextField
                        className="w-100 mb3"
                        id="password"
                        label="Change password"
                        type="password"
                        variant="outlined"
                        value={password}
                        onChange={(e) => setPassword(e.target.value)}
                      />
                      <Button variant="contained" size="large" className="w-100" type="submit">
                        Update Profile
                      </Button>
                    </form>
                  </Grid>
                </Grid>
              </CardContent>
            </Card>
          </Grid>
        </Grid>
      </Wrapper>
    </>
  );
};
export default Myaccount;
