import React, { useEffect, useState } from "react";
import {
  Card,
  Grid,
  Typography,
  Button,
  TextField,
  Snackbar,
  Alert,
  TableCell,
  TableRow,
  IconButton,
  Switch,
} from "@mui/material";
import AddIcon from "@mui/icons-material/Add";
import EditIcon from "@mui/icons-material/Edit";
import PlaceIcon from "@mui/icons-material/Place";

// Core components
import Header from "../../header";
import Wrapper from "../../wrapper";
import Loaders from "../../loader";
import CustomModal from "../../CustomModal";
import CustomTable from "../../CustomTable";

//api
import DataService from "../../services/requestApi";
import queryString from "query-string";
import { useNavigate } from "react-router-dom";

import { socket } from "../../services/socket";
const State = (props) => {
  const navigate = useNavigate();
  const [modal, setModal] = useState(false);
  const [loading, setLoading] = useState(false);
  const [stateData, setStateData] = useState([]);
  const [mounted, setMounted] = useState(true);
  const [state, setState] = useState();
  const [error, setError] = useState(false);
  const [errorMsg, setErrorMsg] = useState("");
  const [updateStatus, setUpdateStatus] = useState(false);
  const [currentStateId, setCurrentStateId] = useState("");
  useEffect(() => {
    const GetData = async () => {
      setLoading(true);
      try {
        const res = await DataService.StateAll();
        if (mounted) {
          setStateData(res.data.data);
        }
        setLoading(false);
      } catch (e) {
        console.log(e);
      }
    };
    GetData();
    return () => {
      GetData();
      setMounted(false);
    };
  }, [mounted]);

  const ToggleModal = () => {
    setModal(!modal);
    if (!modal) {
      setState();
      setCurrentStateId();
      setUpdateStatus(false);
    }
  };

  const UpdateMode = (e, data) => {
    ToggleModal();
    setUpdateStatus(true);
    setState(data.name);
    setCurrentStateId(data._id);
  };
  function RemoveData() {
    setState();
    setCurrentStateId();
    setUpdateStatus(false);
    setMounted(true);
    ToggleModal();
    socket.emit("change", "allState", (error) => {
      if (error) {
        alert(error);
      }
    });
  }
  const SubmitDetails = async (e) => {
    e.preventDefault();
    setLoading(true);
    try {
      const Data = {
        name: state,
      };
      const res = await DataService.StateCreate(queryString.stringify(Data));
      if (res.data.status_code === 400) {
        setError(true);
        setErrorMsg(res.data.message);
      } else {
        RemoveData();
        setLoading(false);
      }
    } catch (e) {
      console.log(e);
    }
  };
  const UpdateDetails = async (e) => {
    e.preventDefault();
    setLoading(true);
    try {
      ToggleModal();
      var Data = {
        name: state,
      };

      const res = await DataService.StateUpdate(currentStateId, queryString.stringify(Data));

      if (res.data.status_code === 400) {
        setError(true);
        setErrorMsg(res.data.message);
      } else {
        RemoveData();
        setLoading(false);
      }
    } catch (e) {
      console.log(e);
    }
  };
  const tableHead = ["Id", "Name", "Status", "Location/City", "Action"];

  const offBid = async (e, data) => {
    try {
      var Data = {
        bid_status: !data.bid_status,
      };
      const res = await DataService.StateUpdate(data._id, queryString.stringify(Data));
      if (res.data.status_code === 200) {
        socket.emit("tickets", data._id, (error) => {
          if (error) {
            alert(error);
          }
        });
      }
      setMounted(true);
    } catch (e) {
      console.log(e);
    }
  };
  return (
    <>
      {error && (
        <Snackbar open={error} autoHideDuration={4000}>
          <Alert severity="error">{errorMsg}</Alert>
        </Snackbar>
      )}
      <Header />
      <Loaders open={loading} />
      <Wrapper>
        <Grid container justifyContent="center" alignItems={"center"}>
          <Grid item xl={11} lg={11} xs={12}>
            <div className="mb3">
              <Grid container justifyContent="center" alignItems={"center"}>
                <Grid item xl={8} lg={8} xs={12}>
                  <Typography variant="h5">All State</Typography>
                </Grid>
                <Grid item xl={4} lg={4} xs={12}>
                  <div className="wallet" style={{ textAlign: "right", display: "block" }}>
                    <Button variant="outlined" onClick={ToggleModal}>
                      <AddIcon />
                      Add State
                    </Button>
                  </div>
                </Grid>
              </Grid>
            </div>
            <Card className="mb3">
              <CustomTable tablehead={tableHead}>
                {stateData &&
                  stateData.map((item, index) => (
                    <TableRow key={item._id}>
                      <TableCell>{index + 1}</TableCell>
                      <TableCell>{item.name}</TableCell>
                      <Switch checked={item?.bid_status} onChange={(e) => offBid(e, item)} />
                      <TableCell>
                        <IconButton onClick={(e) => navigate("/locations", { state: item })}>
                          <PlaceIcon fontSize="small" color="warning" />
                        </IconButton>
                      </TableCell>
                      <TableCell>
                        <IconButton
                          onClick={(e) => UpdateMode(e, item)}
                          style={{ padding: 0, marginRight: 10 }}
                        >
                          <EditIcon fontSize="small" />
                        </IconButton>

                        <Button
                          onClick={() => navigate("/party", { state: item })}
                          variant="outlined"
                        >
                          All parties
                        </Button>
                      </TableCell>
                    </TableRow>
                  ))}
              </CustomTable>
            </Card>
          </Grid>
        </Grid>
      </Wrapper>
      <CustomModal
        open={modal}
        handleClose={ToggleModal}
        title={updateStatus ? "Edit State" : "Add State"}
      >
        <Wrapper>
          <Grid container justifyContent="center" alignItems={"center"}>
            <Grid item xl={4}>
              <form className="formbox" onSubmit={updateStatus ? UpdateDetails : SubmitDetails}>
                <TextField
                  className="w-100 mb3"
                  id="partySeats"
                  label="Enter State Name"
                  type="text"
                  variant="outlined"
                  value={state}
                  onChange={(e) => setState(e.target.value)}
                />

                {!updateStatus ? (
                  <Button
                    type="submit"
                    variant="contained"
                    className="w-100"
                    onClick={SubmitDetails}
                  >
                    Submit
                  </Button>
                ) : (
                  <Button
                    type="submit"
                    variant="contained"
                    className="w-100"
                    onClick={UpdateDetails}
                  >
                    Update
                  </Button>
                )}
              </form>
            </Grid>
          </Grid>
        </Wrapper>
      </CustomModal>
    </>
  );
};
export default State;
