import React, { useEffect, useState } from "react";
import { Card, Grid, Typography, Button, TextField, Snackbar, Alert } from "@mui/material";
import AddIcon from "@mui/icons-material/Add";
import { useLocation } from "react-router-dom";

// Core components
import Header from "../../header";
import Wrapper from "../../wrapper";
import Loaders from "../../loader";
import CustomModal from "../../CustomModal";
import AllCandidates from "../../candidates/allCandidates";

//api
import DataService from "../../services/requestApi";
import queryString from "query-string";
import { socket } from "../../services/socket";

const Candidates = (props) => {
  let { state } = useLocation();

  const [list, setList] = useState([]);
  const [loading, setLoading] = useState(false);
  const [mounted, setMounted] = useState(true);
  const [modal, setModal] = useState(false);
  const [updateStatus, setUpdateStatus] = useState(false);
  const [candidates_id, setCandidatesId] = useState("");
  const [names, setNames] = useState("");
  const [party_name, setPartyName] = useState("");
  const [error, setError] = useState(false);
  const [errorMsg, setErrorMsg] = useState("");
  useEffect(() => {
    const GetData = async () => {
      try {
        setLoading(true);
        const res = await DataService.AllStateCandidate(state._id);
        if (mounted) {
          setList(res.data.data);
        }
        setLoading(false);
      } catch (e) {
        console.log(e);
      }
    };
    GetData();
    return () => {
      GetData();
      setMounted(false);
    };
  }, [mounted]);
  const ToggleModal = () => {
    setModal(!modal);
    if (!modal) {
      setUpdateStatus(false);
      setCandidatesId();
      setNames();
      setPartyName();
    }
  };
  function RemoveData() {
    setModal(false);
    setUpdateStatus(false);
    setCandidatesId();
    setNames();
    setPartyName();
    setMounted(true);
    socket.emit("change", "candidateLocationwise", (error) => {
      if (error) {
        alert(error);
      }
    });
  }
  const createCandidate = async (e) => {
    e.preventDefault();
    try {
      const Data = {
        name: names,
        location_id: state._id,
        state_id: state?.state_id?._id,
        party_name,
      };
      const res = await DataService.StateCandidateCreate(queryString.stringify(Data));
      if (res.data.status_code === 200) {
        RemoveData();
      } else if (res.data.status_code === 400) {
        RemoveData();
        setError(true);
        setErrorMsg(res.data.message);
      }
    } catch (e) {
      console.log(e);
    }
  };

  const UpdateStatus = (e, data) => {
    e.preventDefault();
    setUpdateStatus(true);
    setModal(true);
    setCandidatesId(data._id);
    setNames(data.name);
    setPartyName(data.party_name);
  };
  const UpdateCandidate = async (e) => {
    e.preventDefault();
    try {
      const Data = {
        name: names,
        party_name,
      };
      const res = await DataService.StateCandidateUpdate(
        candidates_id,
        queryString.stringify(Data)
      );
      if (res.data.status_code === 200) {
        RemoveData();
      }
    } catch (e) {
      console.log(e);
    }
  };
  const ChangeBidStatus = async (e, data) => {
    e.preventDefault();
    try {
      const Data = {
        bid_status: !data.bid_status,
      };
      const res = await DataService.StateCandidateUpdate(data._id, queryString.stringify(Data));
      console.log(res.data);
      if (res.data.status_code === 200) {
        RemoveData();
      }
    } catch (e) {
      console.log(e);
    }
  };

  return (
    <>
      <Header />
      {error && (
        <Snackbar open={error} autoHideDuration={1000}>
          <Alert severity="error">{errorMsg}</Alert>
        </Snackbar>
      )}
      <Loaders open={loading} />
      <Wrapper>
        <Grid container justifyContent="center" alignItems={"center"}>
          <Grid item xl={11} lg={11} xs={12}>
            <div className="mb3">
              <Grid container justifyContent="center" alignItems={"center"}>
                <Grid item xl={8} lg={8} xs={12}>
                  <Typography variant="h5">All Candidates in {state && state.name}</Typography>
                </Grid>
                <Grid item xl={4} lg={4} xs={12}>
                  <div className="wallet" style={{ textAlign: "right", display: "block" }}>
                    <Button variant="outlined" onClick={ToggleModal}>
                      <AddIcon />
                      Add Candidates in {state && state.name}
                    </Button>
                  </div>
                </Grid>
              </Grid>
            </div>
            <Card className="mb3">
              <AllCandidates
                data={list}
                stateData={state}
                statusChanged={UpdateStatus}
                changebid={ChangeBidStatus}
              />
            </Card>
          </Grid>
        </Grid>
      </Wrapper>
      <CustomModal
        open={modal}
        handleClose={ToggleModal}
        title={updateStatus ? "Update Candidates" : "Add Candidates"}
      >
        <Wrapper>
          <Grid container justifyContent="center" alignItems={"center"}>
            <Grid item xl={4}>
              <form className="formbox" onSubmit={updateStatus ? UpdateCandidate : createCandidate}>
                <TextField
                  className="w-100 mb3"
                  id="partyName"
                  label="Name"
                  type="text"
                  variant="outlined"
                  value={names}
                  onChange={(e) => setNames(e.target.value)}
                  required={!updateStatus}
                />

                <TextField
                  className="w-100 mb3"
                  id="partyName"
                  label="Party Name"
                  type="text"
                  variant="outlined"
                  value={party_name}
                  onChange={(e) => setPartyName(e.target.value)}
                  required={!updateStatus}
                />
                <Button variant="contained" size="large" className="w-100" type="submit">
                  Submit
                </Button>
              </form>
            </Grid>
          </Grid>
        </Wrapper>
      </CustomModal>
    </>
  );
};
export default Candidates;
