import React, { useEffect, useState } from "react";
import {
  Card,
  Grid,
  CardContent,
  Typography,
  Button,
  TextField,
  Snackbar,
  Alert,
} from "@mui/material";
import { useNavigate, useLocation } from "react-router-dom";

// Core components
import Header from "../../header";
import Wrapper from "../../wrapper";

//api
import DataService from "../../services/requestApi";
import queryString from "query-string";
import Loaders from "../../loader";
import { socket } from "../../services/socket";

const EditTickets = () => {
  let navigate = useNavigate();
  let { state } = useLocation();
  const [ticketId, setTicketId] = useState("");
  const [yes_winning, setYesWinning] = useState("");
  const [no_winning, setNoWinning] = useState("");
  const [yes_seats, setYesSeats] = useState("");
  const [no_seats, setNoSeats] = useState("");
  const [error, setError] = useState(false);
  const [names, setName] = useState("");
  const [min_bid, setMinBid] = useState("");
  const [max_bid, setMaxBid] = useState("");
  const [errorMsg, setErrorMsg] = useState("");
  const [loading, setLoading] = useState(false);
  useEffect(() => {
    let Mounted = true;
    setLoading(true);
    const getData = () => {
      if (Mounted) {
        console.log(state);
        setYesWinning(state.data.yes_winning_percent);
        setNoWinning(state.data.no_winning_percent);
        setYesSeats(state.data.yes_seats);
        setNoSeats(state.data.no_seats);
        setName(state.data.name);
        setMinBid(state.data.min_bid);
        setMaxBid(state.data.max_bid);
        setTicketId(state.data._id);
      }
      setLoading(false);
    };
    getData();
    return () => {
      getData();
      Mounted = false;
    };
  }, [state]);
  const SubmitDetails = async (e) => {
    e.preventDefault();
    try {
      const Data = {
        yes_winning_percent: yes_winning,
        no_winning_percent: no_winning,
        yes_seats,
        no_seats,
        name: names,
        min_bid: ~~min_bid,
        max_bid: ~~max_bid,
      };
      const res = await DataService.TicketUpdate(ticketId, queryString.stringify(Data));
      if (res.data.status_code === 400) {
        setError(true);
        setErrorMsg(res.data.message);
      } else {
        socket.emit("change", "partyStatewise", (error) => {
          if (error) {
            alert(error);
          }
        });
        navigate(-1);
      }
    } catch (e) {
      console.log(e);
    }
  };
  return (
    <>
      {error && (
        <Snackbar open={error} autoHideDuration={4000}>
          <Alert severity="error">{errorMsg}</Alert>
        </Snackbar>
      )}
      <Header />
      <Loaders open={loading} />
      <Wrapper>
        <Grid container justifyContent="center" alignItems={"center"}>
          <Grid item xl={6} lg={6} xs={12}>
            <Card className="mb3">
              <CardContent>
                <Typography variant="h5">Edit Tickets</Typography>
                <br />
                <form className="formbox" onSubmit={SubmitDetails}>
                  <Grid container spacing={2}>
                    <Grid item xl={12} lg={12} xs={12}>
                      <TextField
                        className="w-100"
                        id="namess"
                        label="Enter Name"
                        type="text"
                        variant="outlined"
                        onChange={(e) => setName(e.target.value)}
                        value={names}
                      />
                    </Grid>
                    <Grid item xl={6} lg={6} xs={12}>
                      <TextField
                        className="w-100"
                        id="yesSeats"
                        label="Yes Seats"
                        type="text"
                        variant="outlined"
                        onChange={(text) => setYesSeats(text.target.value)}
                        value={yes_seats}
                      />
                    </Grid>

                    <Grid item xl={6} lg={6} xs={12}>
                      <TextField
                        className="w-100"
                        id="NoSeats"
                        label="No Seats"
                        type="text"
                        variant="outlined"
                        onChange={(text) => setNoSeats(text.target.value)}
                        value={no_seats}
                      />
                    </Grid>
                    <Grid item xl={6} lg={6} xs={12}>
                      <TextField
                        className="w-100"
                        id="YesWinningPercent"
                        label="Yes Winning Percent"
                        type="text"
                        variant="outlined"
                        onChange={(e) => setYesWinning(e.target.value)}
                        value={yes_winning}
                      />
                    </Grid>
                    <Grid item xl={6} lg={6} xs={12}>
                      <TextField
                        className="w-100"
                        id="NoWinningPercent"
                        label="No Winning Percent"
                        type="text"
                        variant="outlined"
                        onChange={(text) => setNoWinning(text.target.value)}
                        value={no_winning}
                      />
                    </Grid>

                    <Grid item xl={6} lg={6} xs={12}>
                      <TextField
                        className="w-100"
                        id="min_bid"
                        label="Min bid"
                        type="text"
                        variant="outlined"
                        onChange={(text) => setMinBid(text.target.value)}
                        value={min_bid}
                      />
                    </Grid>
                    <Grid item xl={6} lg={6} xs={12}>
                      <TextField
                        className="w-100"
                        id="max_bid"
                        label="Max bid"
                        type="text"
                        variant="outlined"
                        onChange={(text) => setMaxBid(text.target.value)}
                        value={max_bid}
                      />
                    </Grid>
                    <Grid item xl={12} lg={12} xs={12}>
                      <Button variant="contained" size="large" className="w-100" type="submit">
                        Update Tickets
                      </Button>
                    </Grid>
                  </Grid>
                </form>
              </CardContent>
            </Card>
          </Grid>
        </Grid>
      </Wrapper>
    </>
  );
};
export default EditTickets;
