import React from "react";
import { IconButton, Switch, TableCell, TableRow } from "@mui/material";
import { useNavigate } from "react-router-dom";
import CustomTable from "../CustomTable";
// Icons
import EditIcon from "@mui/icons-material/Edit";
import FactCheckIcon from "@mui/icons-material/FactCheck";
const AllCandidates = ({ data, stateData, statusChanged, changebid }) => {
  const tableHead = [
    "Sr.no",
    "Location/City Name",
    "Name",
    "Party Name",
    "Tickets",
    "Action",
    "Bid Status",
  ];
  let navigate = useNavigate();
  const GotoTicketCandidat = (e, item) => {
    const data = {
      stateData,
      candidatedata: item,
    };
    navigate("/ticketcandidate", {
      state: data,
    });
  };
  console.log(data);
  return (
    <>
      <CustomTable tablehead={tableHead}>
        {data &&
          data.map((item, index) => (
            <TableRow key={item._id}>
              <TableCell>{index + 1}</TableCell>
              <TableCell>{item?.location_id?.name}</TableCell>
              <TableCell>{item.name}</TableCell>
              <TableCell>{item.party_name}</TableCell>
              <TableCell>
                <IconButton onClick={(e) => GotoTicketCandidat(e, item)}>
                  <FactCheckIcon fontSize="small" color="error" />
                </IconButton>
              </TableCell>
              <TableCell>
                <IconButton
                  onClick={(e) => statusChanged(e, item)}
                  disabled={item?.location_id?.result_declare_status}
                >
                  <EditIcon fontSize="small" />
                </IconButton>
              </TableCell>
              <TableCell>
                <Switch
                  checked={item?.bid_status}
                  onChange={(e) => changebid(e, item)}
                  disabled={item?.location_id?.result_declare_status}
                />
              </TableCell>
            </TableRow>
          ))}
      </CustomTable>
    </>
  );
};
export default AllCandidates;
