import React, { useState } from "react";
import { Grid, TextField, Typography, Button, Snackbar, Alert } from "@mui/material";
import { useNavigate } from "react-router-dom";
//Api
import DataService from "../services/requestApi";
import queryString from "query-string";
//socket
import io from "socket.io-client";
import { socket } from "../services/socket";
const ManageWallet = ({ type, adminData }) => {
  let navigate = useNavigate();
  const [coins, setCoins] = useState("");
  const [error, setError] = useState(false);
  const [errorMsg, setErrorMsg] = useState("");
  const SubmitWallet = async (e) => {
    e.preventDefault();

    const Data = {
      admin_id: adminData._id,
      coins,
    };
    try {
      const res = await DataService.ManageWallets(type, queryString.stringify(Data));
      if (res.data.status_code === 200) {
        const DATA = {
          admin_id: adminData._id,
        };
        socket.emit("adminData", DATA, (error) => {
          if (error) {
            alert(error);
          }
        });
        navigate("/");
      } else if (res.data.status_code === 400) {
        setError(true);
        setErrorMsg(res.data.message);
      }
    } catch (e) {
      console.log(e);
    }
  };
  return (
    <Grid container justifyContent="center" alignItems={"center"}>
      {error && (
        <Snackbar open={error} autoHideDuration={4000}>
          <Alert severity="error">{errorMsg}</Alert>
        </Snackbar>
      )}
      <Grid item xl={12} lg={12} xs={12} className="mb3">
        <Typography variant="h5" textTransform="capitalize">
          {type === "transfer"
            ? `Deposit in ${adminData.name} Account`
            : `Withdraw from ${adminData.name} Account`}
        </Typography>
      </Grid>
      <Grid item xl={12}>
        <form className="formbox" onSubmit={SubmitWallet}>
          <TextField
            className="w-100 mb3"
            id="NoSeats"
            label="Enter Points"
            type="text"
            variant="outlined"
            placeholder="Eg:- 500, 1000"
            value={coins}
            onChange={(e) => setCoins(e.target.value)}
          />
          <Button variant="contained" type="submit" size="large" className="w-100">
            Submit
          </Button>
        </form>
      </Grid>
    </Grid>
  );
};
export default ManageWallet;
