import React from "react";
import { IconButton, Switch, TableCell, TableRow } from "@mui/material";
import CustomTable from "../CustomTable";
// Icons
import EditIcon from "@mui/icons-material/Edit";
const AllCandiTickets = ({ data, stateData, statusChanged, changebid }) => {
  const tableHead = [
    "Sr.no",
    "State",
    "Yes Percentage",
    "No Percentage",
    "Min Bid",
    "Max Bid",
    "Action",
  ];
  return (
    <>
      <CustomTable tablehead={tableHead}>
        {data &&
          data.map((item, index) => (
            <TableRow key={item._id}>
              <TableCell>{index + 1}</TableCell>
              <TableCell>{item?.state_id?.name}</TableCell>
              <TableCell>{item.yes_winning_percent}</TableCell>
              <TableCell>{item.no_winning_percent}</TableCell>
              <TableCell>{item.min_bid}</TableCell>
              <TableCell>{item.max_bid}</TableCell>

              <TableCell>
                <IconButton onClick={(e) => statusChanged(e, item)}>
                  <EditIcon fontSize="small" />
                </IconButton>
              </TableCell>
            </TableRow>
          ))}
      </CustomTable>
    </>
  );
};
export default AllCandiTickets;
