import * as React from "react";
import {
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
} from "@mui/material";

const CustomTable = ({ tablehead, children }) => {
  return (
    <TableContainer component={Paper}>
      <Table aria-label="simple table" className="table-striped table-bordered">
        <TableHead>
          <TableRow>
            {tablehead &&
              tablehead.map((item, index) => (
                <TableCell key={index}>{item}</TableCell>
              ))}
          </TableRow>
        </TableHead>
        <TableBody>{children}</TableBody>
      </Table>
    </TableContainer>
  );
};
export default CustomTable;
