import React from "react";
import { Switch, TableCell, TableRow, Chip, Button } from "@mui/material";
import { useNavigate } from "react-router-dom";
//api
// import DataService from "../services/requestApi";
// import queryString from "query-string";

import CustomTable from "../CustomTable";

const UserCards = ({ data, UserStatus }) => {
  const navigate = useNavigate();
  const tableHead = [
    "User Name",
    "Credit Referance",
    "User Code",
    "Balance",
    "Exposure",
    "Client (P/L)",
    "Available Balance",
    "U ST",
    "B ST",
  ];
  const tableHeads = [
    "User Name",
    "Credit Referance",
    "User Code",
    "Balance",
    "Exposure",
    "Client (P/L)",
    "Available Balance",
  ];
  return (
    <>
      <CustomTable tablehead={UserStatus ? tableHead : tableHeads}>
        {data &&
          data.map((item, index) => (
            <TableRow key={item._id}>
              <TableCell>
                <Chip
                  label={item?.name}
                  style={{ backgroundColor: "#464446", color: "#fff" }}
                />
              </TableCell>
              <TableCell>{item?.total_deposit}</TableCell>
              <TableCell>#{item?.code}</TableCell>

              <TableCell>
                <Button
                  style={{ color: "#000" }}
                  onClick={() => navigate("/usertransaction", { state: item })}
                >
                  {item.balance}
                </Button>
              </TableCell>
              <TableCell>
                <Button
                  style={{ color: "#000" }}
                  onClick={() => navigate("/usertransaction", { state: item })}
                >
                  {item.exposure}
                </Button>
              </TableCell>
              <TableCell>
                <Button
                  style={{ color: "#000" }}
                  onClick={() => navigate("/usertransaction", { state: item })}
                >
                  {item.pl}
                </Button>
              </TableCell>
              <TableCell>
                <Button
                  style={{ color: "#000" }}
                  onClick={() => navigate("/usertransaction", { state: item })}
                >
                  {item.wallet}
                </Button>
              </TableCell>
              {UserStatus && (
                <>
                  <TableCell>
                    <Switch
                      checked={item?.status}
                      onChange={(e) => UserStatus(e, "status", item)}
                      inputProps={{ "aria-label": "controlled" }}
                    />
                  </TableCell>
                  <TableCell>
                    <Switch
                      checked={item?.bid_status}
                      onChange={(e) => UserStatus(e, "bid_status", item)}
                      inputProps={{ "aria-label": "controlled" }}
                    />
                  </TableCell>
                </>
              )}
            </TableRow>
          ))}
      </CustomTable>
    </>
  );
};
export default UserCards;
