import React, { useEffect, useState } from "react";
import {
  Grid,
  Card,
  CardContent,
  Typography,
  TableRow,
  TableCell,
} from "@mui/material";
import { useLocation } from "react-router-dom";

// Core components
import Header from "../../header";
import Loaders from "../../loader";
import Wrapper from "../../wrapper";
import CustomTable from "../../CustomTable";
//api
import DataService from "../../services/requestApi";
import moment from "moment";

const TransactionAdmin = (props) => {
  const [list, setList] = useState([]);
  const [loading, setLoading] = useState(false);
  const { state } = useLocation();
  useEffect(() => {
    let Mounted = true;
    const GetData = async () => {
      try {
        setLoading(true);
        const res = await DataService.AdminTransaction(state._id);
        if (Mounted) {
          setList(res.data.data);
          console.log(res.data.data);
        }
        setLoading(false);
      } catch (e) {
        console.log(e);
      }
    };
    GetData();
    return () => {
      Mounted = false;
      GetData();
    };
  }, [state]);
  const tableHead = [
    "Sr.no",
    "Admin Code",
    "Admin Name",
    "Method",
    "Wallet",
    "Date",
  ];
  return (
    <>
      <Header />
      <Loaders open={loading} />
      <Wrapper>
        <Grid container justifyContent="center" alignItems={"center"}>
          <Grid item xl={11} lg={11} xs={12}>
            <Typography variant="h5" className="mb3">
              Transaction History of {state?.name}
            </Typography>
            <Card className="mb3">
              <CustomTable tablehead={tableHead}>
                {list &&
                  list.map((item, index) => (
                    <TableRow key={item._id}>
                      <TableCell scope="row">{index + 1}. </TableCell>
                      <TableCell># {item?.from_id?.code}</TableCell>
                      <TableCell>{item?.from_id?.name}</TableCell>
                      <TableCell>
                        <span
                          style={
                            item.mode === "transfer"
                              ? {
                                  color: "#47c44c",
                                  display: "block",
                                  textTransform: "capitalize",
                                }
                              : {
                                  color: "#ff4545",
                                  display: "block",
                                  textTransform: "capitalize",
                                }
                          }
                        >
                          {item.mode === "transfer"
                            ? item.mode + ` to ${item.to_id?.name}`
                            : item.mode + ` from your side`}
                        </span>
                      </TableCell>
                      <TableCell>
                        <span
                          style={
                            item.mode === "transfer"
                              ? { color: "#47c44c" }
                              : { color: "#ff4545" }
                          }
                        >
                          {item.mode === "transfer" ? "+" : "-"} {item.coins}
                        </span>
                      </TableCell>
                      <TableCell>
                        {moment(item.created_at).format("ll")}
                      </TableCell>
                    </TableRow>
                  ))}
              </CustomTable>
            </Card>
          </Grid>
        </Grid>
      </Wrapper>
    </>
  );
};
export default TransactionAdmin;
