import http from "./http-common";

class DataService {
  // Login Services
  LoginAdmin(data) {
    return http.get("super_admin/login?" + data);
  }

  OwnerData() {
    return http.get("super_admin/data");
  }

  // Admin dataService
  AdminAdd(data) {
    return http.post("super_admin/admin/create", data);
  }
  AdminUpdate(id, data) {
    return http.patch("super_admin/admin/update/" + id, data);
  }

  SuperAdminFetchAll() {
    return http.get("super_admin/admin/all");
  }
  SuperAdminFetchAllUser() {
    return http.get("super_admin/user/all");
  }
  CheckAdminExists(code) {
    return http.post("super_admin/check_admin", code);
  }
  SuperAdminUpdateUser(id, data) {
    return http.patch("super_admin/user/update/" + id, data);
  }
  SuperAdminFetchAlladmin(id) {
    return http.get("super_admin/admin/user/" + id);
  }
  FetchAlladminUser(id) {
    return http.get("super_admin/admin/admin/user/" + id);
  }
  ChangeProfile(data) {
    return http.patch("super_admin/update", data);
  }
  ManageWallets(type, data) {
    return http.post(`super_admin/${type}/admin`, data);
  }
  SuperAdminTransaction() {
    return http.get("super_admin/transaction/my");
  }
  AdminTransaction(id) {
    return http.get("super_admin/admin/transaction/" + id);
  }
  UserTransaction(id) {
    return http.get("super_admin/user/transaction/" + id);
  }
  AllAccountStatement(data) {
    return http.post("super_admin/transaction/all", data);
  }
  // State dataService
  StateAll() {
    return http.get("state/all");
  }
  StateCreate(data) {
    return http.post("state/create", data);
  }
  StateUpdate(id, data) {
    return http.patch("state/update/" + id, data);
  }
  AllStateCandidate(id) {
    return http.get("candidate/location/" + id);
  }

  //StateLocation
  AllStateLocation(id) {
    return http.get("location/state/" + id);
  }
  StateLocationCreate(data) {
    return http.post("location/create/", data);
  }
  StateLocationUpdate(id, data) {
    return http.patch("location/update/" + id, data);
  }
  StateLocationResultDeclare(id, data) {
    return http.patch("location/result/" + id, data);
  }
  StateCandidateCreate(data) {
    return http.post("candidate/create/", data);
  }
  StateCandidateUpdate(id, data) {
    return http.patch("candidate/update/" + id, data);
  }
  AllCandidateTicket(id) {
    return http.get("ticket_candidate/candidate/" + id);
  }
  CandidateCreateTicket(data) {
    return http.post("ticket_candidate/create/", data);
  }
  CandidateUpdateTicket(id, data) {
    return http.patch("ticket_candidate/update/" + id, data);
  }
  // party dataService

  PartyAll() {
    return http.get("party/all");
  }
  AllPartyState(id) {
    return http.get("party/state/" + id);
  }
  PartyCreate(data) {
    return http.post("party/create", data);
  }
  PartyUpdate(id, data) {
    return http.patch("party/update/" + id, data);
  }
  PartyResult(id, data) {
    return http.patch("party/result/" + id, data);
  }
  DeclareResult(id) {
    return http.get("super_admin/ticket/bid_result/" + id);
  }
  GetTickeyByParty(id) {
    return http.get("ticket/party/" + id);
  }
  GetBidByTickets(id) {
    return http.get("ticket/bid/" + id);
  }
  TicketCreate(data) {
    return http.post("ticket/create", data);
  }
  TicketUpdate(id, data) {
    return http.patch("ticket/update/" + id, data);
  }
  TicketDelete(id) {
    return http.delete("ticket/delete/" + id);
  }
}

export default new DataService();
