import React from "react";
import moment from "moment";
import { ListItem, ListItemText, ListItemAvatar, Badge } from "@mui/material";
import Avatar from "@mui/material/Avatar";
import { deepOrange } from "@mui/material/colors";
import AccountBalanceWalletIcon from "@mui/icons-material/AccountBalanceWallet";
// Icons
import PersonOutlineIcon from "@mui/icons-material/PersonOutline";

const BidCards = ({ data }) => {
  function FilterName(name) {
    const partyname = JSON.parse(name?.ticket_data);
    // console.log(name?.user_id.name, partyname?.name, partyname?.party_id?.name);
    // console.log(name);
    return `${name?.user_id.name} Bid on ( ${partyname?.name}, ${partyname?.party_id?.name} ) -  () `;
  }
  return (
    <>
      <ListItem>
        <ListItemAvatar>
          <Avatar sx={{ bgcolor: deepOrange[500] }}>
            <PersonOutlineIcon />
          </Avatar>
        </ListItemAvatar>
        <ListItemText
          primary={
            <>
              {FilterName(data)} <Badge color="primary" badgeContent={data?.yes_or_no}></Badge>
            </>
          }
          secondary={
            <>
              <span style={data?.bid_status === "win" ? { color: "green" } : { color: "red" }}>
                {data?.bid_status === "win" ? (
                  <>Win {data?.winning_amount} Points</>
                ) : (
                  data?.bid_status
                )}
              </span>
              <span
                style={{
                  display: "block",
                  fontSize: "13px",
                  marginTop: "5px",
                }}
              >
                {moment(data?.created_at).format("ll")}
              </span>
            </>
          }
        />
        <div className="wallet">
          <AccountBalanceWalletIcon color={data?.bid_status === "win" ? "success" : "default"} />{" "}
          {data?.bid_amount}
        </div>
      </ListItem>
    </>
  );
};
export default BidCards;
