import React, { useEffect, useState } from "react";
import { Card, Grid, Typography, Button, TextField, CardContent } from "@mui/material";
import AddIcon from "@mui/icons-material/Add";
import { useLocation } from "react-router-dom";

// Core components
import Header from "../../header";
import Wrapper from "../../wrapper";

//api
import DataService from "../../services/requestApi";
import queryString from "query-string";
import Loaders from "../../loader";
import CustomModal from "../../CustomModal";
import AllCandiTickets from "../../candidates/allCandiTickets";
import { socket } from "../../services/socket";
const TicketCandidate = (props) => {
  let { candidatedata, stateData } = useLocation().state;

  const [list, setList] = useState([]);
  const [loading, setLoading] = useState(false);
  const [mounted, setMounted] = useState(true);
  const [modal, setModal] = useState(false);
  const [updateStatus, setUpdateStatus] = useState(false);
  const [candidatesTicketId, setCandidatesTicketId] = useState("");
  const [yes_winning, setYesWinning] = useState("");
  const [no_winning, setNoWinning] = useState("");
  const [min_bid, setMinBid] = useState("");
  const [max_bid, setMaxBid] = useState("");

  useEffect(() => {
    const GetData = async () => {
      try {
        setLoading(true);
        const res = await DataService.AllCandidateTicket(candidatedata._id);
        if (mounted) {
          setList(res.data.data);
        }
        setLoading(false);
      } catch (e) {
        console.log(e);
      }
    };
    GetData();
    return () => {
      GetData();
      setMounted(false);
    };
  }, [mounted]);
  const ToggleModal = () => {
    setModal(!modal);
    if (!modal) {
      setUpdateStatus(false);
      setCandidatesTicketId();
      setYesWinning();
      setNoWinning();
      setMinBid();
      setMaxBid();
    }
  };
  function RemoveData() {
    setModal(false);
    setUpdateStatus(false);
    setCandidatesTicketId();
    setYesWinning();
    setNoWinning();
    setMinBid();
    setMaxBid();
    setMounted(true);
    socket.emit("change", "candidateLocationwise", (error) => {
      if (error) {
        alert(error);
      }
    });
  }
  const createCandidateTicket = async (e) => {
    e.preventDefault();
    try {
      const Data = {
        candidate_id: candidatedata._id,
        state_id: stateData?.state_id?._id,
        location_id: candidatedata?.location_id._id,
        yes_winning_percent: yes_winning,
        no_winning_percent: no_winning,
        min_bid: ~~min_bid,
        max_bid: ~~max_bid,
      };
      const res = await DataService.CandidateCreateTicket(queryString.stringify(Data));
      console.log(res.data);
      if (res.data.status_code === 200) {
        RemoveData();
      }
    } catch (e) {
      console.log(e);
    }
  };
  const UpdateStatus = (e, data) => {
    e.preventDefault();
    setUpdateStatus(true);
    setModal(true);
    setCandidatesTicketId(data._id);
    setYesWinning(data.yes_winning_percent);
    setNoWinning(data.no_winning_percent);
    setMinBid(data.min_bid);
    setMaxBid(data.max_bid);
  };
  const UpdateCandidateTicket = async (e) => {
    e.preventDefault();
    try {
      const Data = {
        yes_winning_percent: yes_winning,
        no_winning_percent: no_winning,
        min_bid: ~~min_bid,
        max_bid: ~~max_bid,
      };
      const res = await DataService.CandidateUpdateTicket(
        candidatesTicketId,
        queryString.stringify(Data)
      );
      if (res.data.status_code === 200) {
        RemoveData();
      }
    } catch (e) {
      console.log(e);
    }
  };

  return (
    <>
      <Header />
      <Loaders open={loading} />
      <Wrapper>
        <Grid container justifyContent="center" alignItems={"center"}>
          <Grid item xl={11} lg={11} xs={12}>
            <div className="mb3">
              <Grid container justifyContent="center" alignItems={"center"}>
                <Grid item xl={8} lg={8} xs={12}>
                  <Typography variant="h5">
                    All Ticket of {candidatedata && candidatedata.name}
                  </Typography>
                </Grid>
                <Grid item xl={4} lg={4} xs={12}>
                  <div className="wallet" style={{ textAlign: "right", display: "block" }}>
                    {!list.length > 0 && (
                      <Button variant="outlined" onClick={ToggleModal}>
                        <AddIcon />
                        Add Ticket in {candidatedata && candidatedata.name}
                      </Button>
                    )}
                  </div>
                </Grid>
              </Grid>
            </div>
            <Card className="mb3">
              <AllCandiTickets data={list} statusChanged={UpdateStatus} />
            </Card>
          </Grid>
        </Grid>
      </Wrapper>
      <CustomModal
        open={modal}
        handleClose={ToggleModal}
        title={updateStatus ? "Update Candidates" : "Add Candidates"}
      >
        <Wrapper>
          <Grid container justifyContent="center" alignItems={"center"}>
            <Grid item xl={6} lg={6} xs={12}>
              <Card className="mb3">
                <CardContent>
                  <form
                    className="formbox"
                    onSubmit={updateStatus ? UpdateCandidateTicket : createCandidateTicket}
                  >
                    <Grid container spacing={2}>
                      <Grid item xl={6}>
                        <TextField
                          className="w-100"
                          id="YesWinningPercent"
                          label="Yes Winning Percent"
                          type="text"
                          variant="outlined"
                          onChange={(e) => setYesWinning(e.target.value)}
                          value={yes_winning}
                          autoComplete="new-win"
                          required={!updateStatus}
                        />
                      </Grid>
                      <Grid item xl={6}>
                        <TextField
                          className="w-100"
                          id="NoWinningPercent"
                          label="No Winning Percent"
                          type="text"
                          variant="outlined"
                          onChange={(text) => setNoWinning(text.target.value)}
                          value={no_winning}
                          autoComplete="new-no-win"
                          required={!updateStatus}
                        />
                      </Grid>
                      <Grid item xl={6}>
                        <TextField
                          className="w-100"
                          id="min_bid"
                          label="Min bid"
                          type="number"
                          variant="outlined"
                          onChange={(text) => setMinBid(text.target.value)}
                          value={min_bid}
                          autoComplete="new-min"
                          required={!updateStatus}
                        />
                      </Grid>
                      <Grid item xl={6}>
                        <TextField
                          className="w-100"
                          id="max_bid"
                          label="Max bid"
                          type="number"
                          variant="outlined"
                          onChange={(text) => setMaxBid(text.target.value)}
                          value={max_bid}
                          autoComplete="new-max"
                          required={!updateStatus}
                        />
                      </Grid>
                      <Grid item xl={12}>
                        <Button variant="contained" size="large" className="w-100" type="submit">
                          Submit
                        </Button>
                      </Grid>
                    </Grid>
                  </form>
                </CardContent>
              </Card>
            </Grid>
          </Grid>
        </Wrapper>
      </CustomModal>
    </>
  );
};
export default TicketCandidate;
