import React, { useEffect, useState } from "react";
import { Card, Grid, Typography, Button, TextField, Autocomplete } from "@mui/material";
import AddIcon from "@mui/icons-material/Add";
import { useLocation } from "react-router-dom";
// Core components
import Header from "../../header";
import Wrapper from "../../wrapper";

//api
import DataService from "../../services/requestApi";
import queryString from "query-string";
import Loaders from "../../loader";
import CustomModal from "../../CustomModal";
import AllStateLocation from "../../Locations/allStateLocation";
import { socket } from "../../services/socket";
const Locations = (props) => {
  let { state } = useLocation();

  const [list, setList] = useState([]);
  const [candidatelist, setCandidateList] = useState([]);
  const [candidateid, setCandidateId] = useState("1");
  const [candidateloading, setCandidateLoading] = useState(false);
  const [loading, setLoading] = useState(false);
  const [mounted, setMounted] = useState(true);
  const [modal, setModal] = useState(false);
  const [candidateModal, setCandidateModal] = useState(false);
  const [updateStatus, setUpdateStatus] = useState(false);
  const [location_id, setLocationId] = useState("");
  const [current_locationname, setCurrentLocationName] = useState("");
  const [names, setNames] = useState("");

  useEffect(() => {
    const GetData = async () => {
      try {
        setLoading(true);
        const res = await DataService.AllStateLocation(state._id);
        if (mounted) {
          setList(res.data.data);
        }
        setLoading(false);
      } catch (e) {
        console.log(e);
      }
    };
    GetData();
    return () => {
      GetData();
      setMounted(false);
    };
  }, [mounted]);
  const ToggleModal = () => {
    setModal(!modal);
    if (!modal) {
      setUpdateStatus(false);
      setLocationId();
      setNames();
    }
  };
  function RemovData() {
    setMounted(true);
    setUpdateStatus(false);
    setLocationId();
    setNames();
    socket.emit("change", "locationViaStateId", (error) => {
      if (error) {
        alert(error);
      }
    });
    ToggleModal();
  }
  const createLocation = async (e) => {
    e.preventDefault();
    try {
      const Data = {
        name: names,
        state_id: state._id,
      };
      const res = await DataService.StateLocationCreate(queryString.stringify(Data));
      if (res.data.status_code === 200) {
        // window.location.reload(true);
        RemovData();
      }
    } catch (e) {
      console.log(e);
    }
  };
  const UpdateStatus = (e, data) => {
    e.preventDefault();
    setUpdateStatus(true);
    setModal(true);
    setLocationId(data._id);
    setNames(data.name);
  };
  const UpdateLocations = async (e) => {
    e.preventDefault();
    try {
      const Data = {
        name: names,
      };
      const res = await DataService.StateLocationUpdate(location_id, queryString.stringify(Data));
      if (res.data.status_code === 200) {
        // window.location.reload(true);
        RemovData();
      }
    } catch (e) {
      console.log(e);
    }
  };
  const BidOff = async (e, data) => {
    e.preventDefault();
    try {
      const Data = {
        bid_status: !data.bid_status,
      };
      const res = await DataService.StateLocationUpdate(data._id, queryString.stringify(Data));
      if (res.data.status_code === 200) {
        // window.location.reload(true);

        socket.emit("change", "locationViaStateId", (error) => {
          if (error) {
            alert(error);
          }
        });
        setMounted(true);
      }
    } catch (e) {
      console.log(e);
    }
  };
  const DeclareResult = async (e) => {
    e.preventDefault();
    setCandidateLoading(true);
    try {
      const Data = {
        candidate_winner_id: candidateid,
      };
      const res = await DataService.StateLocationResultDeclare(
        location_id,
        queryString.stringify(Data)
      );
      console.log(res.data);
      if (res.data.status_code === 200) {
        socket.emit("change", "locationResult", (error) => {
          if (error) {
            alert(error);
          }
        });
        setCandidateLoading(false);
        HandleCandidateModal();
        setCurrentLocationName();
        HandleCandidateModal();
        setCandidateList();
        setCandidateId();
      }
    } catch (e) {
      console.log(e);
    }
  };
  // console.log(state);
  function HandleCandidateModal() {
    setCandidateModal(!candidateModal);
  }
  const GetCurrentLocationData = (e, data) => {
    console.log(data);
    setLocationId(data._id);
    GetData(data._id);
    setCurrentLocationName(data.name);
    HandleCandidateModal();
  };
  const GetData = async (id) => {
    try {
      setLoading(true);
      if (id) {
        const res = await DataService.AllStateCandidate(id);
        setCandidateList(res.data.data);
      }
      setLoading(false);
    } catch (e) {
      console.log(e);
    }
  };
  return (
    <>
      <Header />
      <Loaders open={loading} />
      <Wrapper>
        <Grid container justifyContent="center" alignItems={"center"}>
          <Grid item xl={11} lg={11} xs={12}>
            <div className="mb3">
              <Grid container justifyContent="center" alignItems={"center"}>
                <Grid item xl={8} lg={8} xs={12}>
                  <Typography variant="h5">All Locations in {state && state.name}</Typography>
                </Grid>
                <Grid item xl={4} lg={4} xs={12}>
                  <div className="wallet" style={{ textAlign: "right", display: "block" }}>
                    <Button variant="outlined" onClick={ToggleModal}>
                      <AddIcon />
                      Add Locations in {state && state.name}
                    </Button>
                  </div>
                </Grid>
              </Grid>
            </div>
            <Card className="mb3">
              <AllStateLocation
                data={list}
                stateData={state}
                statusChanged={UpdateStatus}
                bidoff={BidOff}
                openModal={GetCurrentLocationData}
              />
            </Card>
          </Grid>
        </Grid>
      </Wrapper>
      <CustomModal
        open={modal}
        handleClose={ToggleModal}
        title={updateStatus ? "Update Locations" : "Add Locations"}
      >
        <Wrapper>
          <Grid container justifyContent="center" alignItems={"center"}>
            <Grid item xl={4}>
              <form className="formbox" onSubmit={updateStatus ? UpdateLocations : createLocation}>
                <TextField
                  className="w-100 mb3"
                  id="partyName"
                  label="Name"
                  type="text"
                  variant="outlined"
                  value={names}
                  onChange={(e) => setNames(e.target.value)}
                />
                <Button variant="contained" size="large" className="w-100" type="submit">
                  Submit
                </Button>
              </form>
            </Grid>
          </Grid>
        </Wrapper>
      </CustomModal>
      {/* // Candidates List  */}
      <CustomModal
        open={candidateModal}
        handleClose={HandleCandidateModal}
        title={`Candidates list in ${current_locationname}`}
      >
        <Wrapper>
          <Grid container justifyContent="center" alignItems={"center"}>
            <Grid item xl={4}>
              <form className="formbox" onSubmit={DeclareResult}>
                <Autocomplete
                  disablePortal
                  id="clientList"
                  className="mb3"
                  options={candidatelist}
                  renderInput={(params) => <TextField {...params} label="Search Client" />}
                  getOptionLabel={(option) => `${option.name} -- ${option.party_name}`}
                  onChange={(event, newInputValue) => {
                    setCandidateId(newInputValue._id);
                  }}
                />
                <Button
                  variant="contained"
                  size="large"
                  className="w-100"
                  type="submit"
                  disabled={candidateloading}
                >
                  Decalare Results
                </Button>
              </form>
            </Grid>
          </Grid>
        </Wrapper>
      </CustomModal>
    </>
  );
};
export default Locations;
