import React, { useEffect, useState } from "react";
import { Card, Grid, CardContent, Typography, Button, Snackbar, Alert } from "@mui/material";
import AddIcon from "@mui/icons-material/Add";
import { useLocation, useNavigate } from "react-router-dom";

// Core components
import Header from "../../header";
import Wrapper from "../../wrapper";
import Loaders from "../../loader";
import AllTickets from "../../Tickets/AllTickets";

//api
import DataService from "../../services/requestApi";
import queryString from "query-string";
import { socket } from "../../services/socket";
const Tickets = (props) => {
  let navigate = useNavigate();
  let { state } = useLocation();
  const [ticketData, setTicketData] = useState([]);
  const [loading, setLoading] = useState(false);
  const [mounted, setMounted] = useState(true);
  const [error, setError] = useState(false);
  const [errorMsg, setErrorMsg] = useState("");
  useEffect(() => {
    const GetData = async () => {
      try {
        setLoading(true);
        const res = await DataService.GetTickeyByParty(state._id);
        if (mounted) {
          setTicketData(res.data.data);
        }
        setLoading(false);
      } catch (e) {
        console.log(e);
      }
    };
    GetData();
    return () => {
      GetData();
      setMounted(false);
    };
  }, [mounted]);
  const ChangeBidStatus = async (e, data) => {
    try {
      const Data = {
        bid_status: !data.bid_status,
      };
      const res = await DataService.TicketUpdate(data._id, queryString.stringify(Data));
      if (res.data.status_code === 200) {
        socket.emit("change", "partyStatewise", (error) => {
          if (error) {
            alert(error);
          }
        });
      } else {
        setError(true);
        setErrorMsg(res.data.message);
      }
      setMounted(true);
    } catch (e) {
      console.log(e);
    }
  };
  const DeleteTickets = async (e, id) => {
    setLoading(true);
    try {
      const res = await DataService.TicketDelete(id);
      if (res.data.status_code === 200) {
        socket.emit("change", "partyStatewise", (error) => {
          if (error) {
            alert(error);
          }
        });
        setMounted(true);
        setLoading(false);
      } else {
        setError(true);
        setErrorMsg(res.data.message);
        setLoading(false);
      }
    } catch (e) {
      console.log(e);
    }
  };
  const ResultDeclare = async (e, id) => {
    try {
      const res = await DataService.DeclareResult(id);
      if (res.data.message) {
        setError(true);
        setErrorMsg(res.data.message);
        setMounted(true);
      }
    } catch (e) {
      console.log(e);
    }
  };
  const NaviGate = () => {
    navigate("/addtickets", {
      state: state,
    });
  };

  return (
    <>
      {error && (
        <Snackbar open={error} autoHideDuration={4000}>
          <Alert severity="error">{errorMsg}</Alert>
        </Snackbar>
      )}
      <Header />
      <Loaders open={loading} />
      <Wrapper>
        <Grid container justifyContent="center" alignItems={"center"}>
          <Grid item xl={11} lg={11} xs={12}>
            <Card className="mb3">
              <CardContent>
                <Grid container justifyContent="center" alignItems={"center"}>
                  <Grid item xl={8} lg={8} xs={12}>
                    <Typography variant="h5">
                      All Tickets of {state.state_id?.name}, {state?.name}
                    </Typography>
                  </Grid>
                  <Grid item xl={4} lg={4} xs={12}>
                    <div className="wallet" style={{ textAlign: "right", display: "block" }}>
                      <Button variant="outlined" onClick={NaviGate}>
                        <AddIcon />
                        Add Tickets
                      </Button>
                    </div>
                  </Grid>
                </Grid>
              </CardContent>
            </Card>
            <Card className="mb3">
              <AllTickets
                stateData={state.state_id}
                data={ticketData}
                changebid={ChangeBidStatus}
                deletetickets={DeleteTickets}
                ResultDeclare={ResultDeclare}
              />
            </Card>
          </Grid>
        </Grid>
      </Wrapper>
    </>
  );
};
export default Tickets;
