import React, { useEffect, useState } from "react";
import {
  Card,
  Grid,
  CardContent,
  Typography,
  Button,
  TextField,
  Snackbar,
  Alert,
} from "@mui/material";
import AddIcon from "@mui/icons-material/Add";
import { useLocation } from "react-router-dom";

// Core components
import Header from "../../header";
import Wrapper from "../../wrapper";
import AllParty from "../../Party/AllParty";
//api
import DataService from "../../services/requestApi";
import queryString from "query-string";
import Loaders from "../../loader";
import CustomModal from "../../CustomModal";
import { socket } from "../../services/socket";
const Party = (props) => {
  let { state } = useLocation();
  const [modal, setModal] = useState(false);
  const [winmodal, setWinModal] = useState(false);
  const [partyData, setPartyData] = useState([]);
  const [partyId, setPartyId] = useState("");
  const [loading, setLoading] = useState(false);
  const [mounted, setMounted] = useState(true);
  const [winning_seats, setWinningSeats] = useState();
  const [partyname, setPartyName] = useState("");
  const [error, setError] = useState(false);
  const [errorMsg, setErrorMsg] = useState("");
  const [stateData, setStateData] = useState([]);
  const [currentPartyId, setCurrentPartyId] = useState("");
  useEffect(() => {
    const GetData = async () => {
      setLoading(true);
      try {
        const [Partyres] = await Promise.all([DataService.AllPartyState(state._id)]);
        if (mounted) {
          setPartyData(Partyres.data.data);
          setStateData(state);
          setCurrentPartyId(state._id);
        }
        setLoading(false);
      } catch (e) {
        console.log(e);
      }
    };
    GetData();
    return () => {
      GetData();
      setMounted(false);
    };
  }, [mounted]);

  const ToggleModal = () => {
    setModal(!modal);
  };
  const ToggleWinModal = (e, id) => {
    setPartyId(id);
    setWinModal(!winmodal);
  };
  function RemoveData() {
    setPartyId();
    setPartyName();
    setMounted(true);
    setWinningSeats();
    setCurrentPartyId();
    ToggleWinModal();
  }
  const ResultSeats = async (e) => {
    e.preventDefault();
    try {
      const Data = {
        winning_seats,
      };
      const res = await DataService.PartyResult(partyId, queryString.stringify(Data));
      if (res.data.status_code === 200) {
        // Toast.show({title: res.data.message, status: 'success'});
        socket.emit("change", "partyResult", (error) => {
          if (error) {
            alert(error);
          }
        });
        setMounted(true);
        setWinningSeats();
        setWinModal(!winmodal);
        RemoveData();
      } else {
        // Toast.show({title: res.data.message, status: 'error'});
        setError(true);
        setErrorMsg(res.data.message);
        RemoveData();
      }
    } catch (e) {
      console.log(e);
    }
  };

  const SubmitDetails = async (e) => {
    e.preventDefault();
    try {
      const Data = {
        state_id: currentPartyId,
        name: partyname,
        seats: 100,
      };
      const res = await DataService.PartyCreate(queryString.stringify(Data));
      if (res.data.status_code === 400) {
        setError(true);
        setErrorMsg(res.data.message);
      } else {
        window.location.reload(true);
      }
    } catch (e) {
      console.log(e);
    }
  };
  return (
    <>
      {error && (
        <Snackbar open={error} autoHideDuration={4000}>
          <Alert severity="error">{errorMsg}</Alert>
        </Snackbar>
      )}
      <Header />
      <Loaders open={loading} />
      <Wrapper>
        <Grid container justifyContent="center" alignItems={"center"}>
          <Grid item xl={11} lg={11} xs={12}>
            <Card className="mb3">
              <CardContent>
                <Grid container justifyContent="center" alignItems={"center"}>
                  <Grid item xl={8} lg={8} xs={12}>
                    <Typography variant="h5">All Party of {stateData.name}</Typography>
                  </Grid>
                  <Grid item xl={4} lg={4} xs={12}>
                    <div className="wallet" style={{ textAlign: "right", display: "block" }}>
                      <Button variant="outlined" onClick={ToggleModal}>
                        <AddIcon />
                        Add Party in {stateData.name}
                      </Button>
                    </div>
                  </Grid>
                </Grid>
              </CardContent>
            </Card>
            <Card className="mb3">
              <AllParty data={partyData} openModal={ToggleWinModal} />
            </Card>
          </Grid>
        </Grid>
      </Wrapper>
      <CustomModal
        open={winmodal}
        handleClose={ToggleWinModal}
        title={"Remember If you Update Seat you cann't declare result again "}
      >
        <Wrapper>
          <Grid container justifyContent="center" alignItems={"center"}>
            <Grid item xl={4}>
              <form className="formbox" onSubmit={ResultSeats}>
                <TextField
                  autoFocus
                  className="w-100 mb3"
                  id="partySeats"
                  label="Wining Seats"
                  type="text"
                  variant="outlined"
                  value={winning_seats}
                  onChange={(e) => setWinningSeats(e.target.value)}
                />

                <Button variant="contained" size="large" className="w-100" type="submit">
                  Submit
                </Button>
              </form>
            </Grid>
          </Grid>
        </Wrapper>
      </CustomModal>
      <CustomModal open={modal} handleClose={ToggleModal} title={"Add Party"}>
        <Wrapper>
          <Grid container justifyContent="center" alignItems={"center"}>
            <Grid item xl={4}>
              <form className="formbox" onSubmit={SubmitDetails}>
                <TextField
                  className="w-100 mb3"
                  id="partyName"
                  label="Party Name"
                  type="text"
                  variant="outlined"
                  value={partyname}
                  onChange={(e) => setPartyName(e.target.value)}
                  required
                />

                <Button variant="contained" size="large" className="w-100" type="submit">
                  Submit
                </Button>
              </form>
            </Grid>
          </Grid>
        </Wrapper>
      </CustomModal>
    </>
  );
};
export default Party;
