import React, { useEffect, useState } from "react";
import {
  Card,
  Grid,
  CardContent,
  Typography,
  Button,
  TextField,
  Snackbar,
  Alert,
} from "@mui/material";
import { useNavigate, useLocation } from "react-router-dom";
// Core components
import Header from "../../header";
import Wrapper from "../../wrapper";

//api
import DataService from "../../services/requestApi";
import queryString from "query-string";
import Loaders from "../../loader";
import { socket } from "../../services/socket";

const EditParty = () => {
  let navigate = useNavigate();
  let location = useLocation();
  const [loading, setLoading] = useState(false);
  const [partyId, setPartyId] = useState("");
  const [partyname, setPartyName] = useState("");
  // const [partyseats, setPartySeats] = useState("");
  const [error, setError] = useState(false);
  const [errorMsg, setErrorMsg] = useState("");
  const [currentStateId, setCurrentStateId] = useState("");
  useEffect(() => {
    let Mounted = true;
    const getData = async () => {
      setLoading(true);
      if (Mounted) {
        setPartyName(location.state.name);
        // setPartySeats(location.state.seats);
        setPartyId(location.state._id);
        setCurrentStateId(location?.state_id?._id);
      }
      setLoading(false);
    };
    getData();
    return () => {
      getData();
      Mounted = false;
    };
  }, [location]);

  const SubmitDetails = async (e) => {
    e.preventDefault();
    try {
      const Data = {
        state_id: currentStateId,
        name: partyname,
        seats: 100,
      };
      const res = await DataService.PartyUpdate(partyId, queryString.stringify(Data));
      if (res.data.status_code === 400) {
        setError(true);
        setErrorMsg(res.data.message);
      } else {
        socket.emit("change", "partyStatewise", (error) => {
          if (error) {
            alert(error);
          }
        });
        navigate(-1);
      }
    } catch (e) {
      console.log(e);
    }
  };

  return (
    <>
      {error && (
        <Snackbar open={error} autoHideDuration={4000}>
          <Alert severity="error">{errorMsg}</Alert>
        </Snackbar>
      )}
      <Header />
      <Loaders open={loading} />
      <Wrapper>
        <Grid container justifyContent="center" alignItems={"center"}>
          <Grid item xl={4} lg={4} xs={12}>
            <Card className="mb3">
              <CardContent>
                <Grid container justifyContent="center" alignItems={"center"}>
                  <Grid item xl={12} lg={12} xs={12} className="mb3">
                    <Typography variant="h5">
                      Edit Party of {location?.state.state_id?.name} , {partyname}
                    </Typography>
                  </Grid>
                  <Grid item xl={12}>
                    <form className="formbox" onSubmit={SubmitDetails}>
                      <TextField
                        className="w-100 mb3"
                        id="partyName"
                        label="Party Name"
                        type="text"
                        variant="outlined"
                        value={partyname}
                        onChange={(e) => setPartyName(e.target.value)}
                      />
                      {/* <TextField
                        className="w-100 mb3"
                        id="partySeats"
                        label="Party Seats"
                        type="text"
                        variant="outlined"
                        value={partyseats}
                        onChange={(e) => setPartySeats(e.target.value)}
                      /> */}

                      <Button variant="contained" size="large" className="w-100" type="submit">
                        Update
                      </Button>
                    </form>
                  </Grid>
                </Grid>
              </CardContent>
            </Card>
          </Grid>
        </Grid>
      </Wrapper>
    </>
  );
};
export default EditParty;
