import React, { useEffect, useState } from "react";
import {
  Grid,
  Card,
  Typography,
  TableRow,
  TableCell,
  Chip,
  Autocomplete,
  TextField,
  Button,
} from "@mui/material";

import moment from "moment";
// Core components
import Header from "../../header";
import Loaders from "../../loader";
import Wrapper from "../../wrapper";
import CustomTable from "../../CustomTable";
//api
import DataService from "../../services/requestApi";
import queryString from "query-string";
const AccountStatement = (props) => {
  const [list, setList] = useState([]);
  const [loading, setLoading] = useState(false);
  const [mounted, setMounted] = useState(true);
  const [userlist, setUserList] = useState([]);
  const [adminlist, setAdminList] = useState([]);
  const [clientType, setClientType] = useState("User");
  const [user_code, setUserCode] = useState("");
  const [from_date, setFromDate] = useState(moment(new Date()).format("YYYY-MM-D"));
  const [to_date, setToDate] = useState(moment(new Date()).format("YYYY-MM-D"));
  const tableHead = ["Date", "Credit", "Debit", "Transaction Mode"];

  const Types = [{ label: "Admin" }, { label: "User" }];
  useEffect(() => {
    const GetData = async () => {
      try {
        setLoading(true);
        const [userRes, adminRes] = await Promise.all([
          DataService.SuperAdminFetchAllUser(),
          DataService.SuperAdminFetchAll(),
        ]);
        if (mounted) {
          setUserList(userRes.data.data);
          setAdminList(adminRes.data.data);
          // console.log(userRes.data);
        }
        setLoading(false);
      } catch (e) {
        console.log(e);
      }
    };
    GetData();
    return () => {
      setMounted(false);
      GetData();
    };
  }, []);
  const GetDataByAdmin = async () => {
    const Data = {
      user_code,
      from_date,
      to_date,
    };
    try {
      setLoading(true);
      const res = await DataService.AllAccountStatement(queryString.stringify(Data));

      setList(res.data.data);
      console.log(res.data.data);
      setLoading(false);
    } catch (e) {
      console.log(e);
    }
  };
  const HandleChange = (e, val) => {
    setClientType(val.label);
    setUserCode("");
  };
  const HandleChangeClient = (e, val) => {
    // setClientType(val.label);
    console.log(val.code);
    setUserCode(val._id);
  };
  return (
    <>
      <Header />
      <Loaders open={loading} />
      <Wrapper>
        <Grid container justifyContent="center" alignItems={"center"}>
          <Grid item xl={11} lg={11} xs={12}>
            <div className="mb3">
              <Typography variant="h5" className="mb3">
                Account Statement
              </Typography>
              <Grid container justifyContent="space-between" alignItems={"center"}>
                <Grid item xl={3} lg={3} xs={3}>
                  <div style={{ paddingRight: "15px" }}>
                    <Autocomplete
                      disablePortal
                      id="clientType"
                      value={clientType}
                      options={Types}
                      renderInput={(params) => <TextField {...params} label="Select Type" />}
                      onChange={HandleChange}
                    />
                  </div>
                </Grid>
                <Grid item xl={3} lg={3} xs={3}>
                  <div style={{ paddingRight: "15px" }}>
                    <Autocomplete
                      disablePortal
                      id="clientList"
                      options={clientType === "User" ? userlist : adminlist}
                      renderInput={(params) => <TextField {...params} label="Search Client" />}
                      getOptionLabel={(option) => option.code}
                      onChange={HandleChangeClient}
                    />
                  </div>
                </Grid>

                <Grid item xl={3} lg={3} xs={3}>
                  <div style={{ paddingRight: "15px" }}>
                    <TextField
                      id="from"
                      label="From"
                      type="date"
                      value={from_date}
                      onChange={(e) => setFromDate(e.target.value)}
                      style={{ width: "100%" }}
                    />
                  </div>
                </Grid>
                <Grid item xl={3} lg={3} xs={3}>
                  <TextField
                    id="to"
                    label="To"
                    type="date"
                    value={to_date}
                    onChange={(e) => setToDate(e.target.value)}
                    style={{ width: "100%" }}
                  />
                </Grid>
              </Grid>
              <div style={{ textAlign: "right" }}>
                <Button variant="contained" style={{ marginTop: 20 }} onClick={GetDataByAdmin}>
                  Load
                </Button>
              </div>
            </div>
            <Card className="mb-3">
              <CustomTable tablehead={tableHead}>
                {list &&
                  list.map((item, index) => (
                    <TableRow key={item._id}>
                      <TableCell>{moment(item.created_at).format("ll")}</TableCell>
                      <TableCell>
                        <span className="text-success">
                          {item.mode === "transfer" || item.mode === "winning"
                            ? `+ ${item.coins}`
                            : "+ 0"}
                        </span>
                      </TableCell>
                      <TableCell>
                        <span className="text-warning">
                          {item.mode === "withdraw" || item.mode === "bidding"
                            ? `- ${item.coins}`
                            : "- 0"}
                        </span>
                      </TableCell>

                      <TableCell>
                        <Chip
                          label={
                            <span className="font-weight-bold text-capitalize">{item?.mode}</span>
                          }
                          style={{ backgroundColor: "#464446", color: "#fff" }}
                        />
                      </TableCell>
                    </TableRow>
                  ))}
              </CustomTable>
            </Card>
          </Grid>
        </Grid>
      </Wrapper>
    </>
  );
};
export default AccountStatement;
