import React from "react";
import { useNavigate } from "react-router-dom";
import { IconButton, Switch, TableCell, TableRow, Chip, Button } from "@mui/material";
//api
import DataService from "../services/requestApi";
import queryString from "query-string";

// import EditIcon from "@mui/icons-material/Edit";
import PeopleAltIcon from "@mui/icons-material/PeopleAlt";
import AccountBalanceWalletIcon from "@mui/icons-material/AccountBalanceWallet";
import { socket } from "../services/socket";
const AdminCard = ({ data, sr, updatemode }) => {
  const navigate = useNavigate();

  const BlockUser = async (e, data, type) => {
    try {
      var Data = {};
      if (type === "status") {
        Data.status = !data.status;
      } else {
        Data.bid_status = !data.bid_status;
      }
      const res = await DataService.AdminUpdate(data._id, queryString.stringify(Data));
      console.log(res.data);
      if (res.data.message === "Success !") {
        const DATA = {
          admin_id: data._id,
        };
        socket.emit("adminData", DATA, (error) => {
          if (error) {
            alert(error);
          }
        });
        window.location.reload(true);
      }
    } catch (e) {
      console.log(e);
    }
  };

  return (
    <>
      <TableRow>
        <TableCell>
          <Chip label={data?.name} style={{ backgroundColor: "#464446", color: "#fff" }} />
        </TableCell>
        <TableCell>{data?.total_deposit}</TableCell>
        <TableCell>
          <Chip label={data?.code} style={{ backgroundColor: "#464446", color: "#fff" }} />
        </TableCell>

        <TableCell>
          <IconButton onClick={(e) => navigate("/allUsers", { state: data })}>
            <PeopleAltIcon />
            <span style={{ fontSize: 15, marginLeft: 5 }}>{data?.user_count}</span>
          </IconButton>
        </TableCell>
        <TableCell>
          <Switch
            checked={data?.status}
            onChange={(e) => BlockUser(e, data, "status")}
            inputProps={{ "aria-label": "controlled" }}
          />
        </TableCell>
        <TableCell>
          <Switch
            checked={data?.bid_status}
            onChange={(e) => BlockUser(e, data, "bid_status")}
            inputProps={{ "aria-label": "controlled" }}
          />
        </TableCell>
        <TableCell>
          <div className="wallet">
            <IconButton onClick={() => navigate("/admintransaction", { state: data })}>
              <AccountBalanceWalletIcon fontSize="small" color="success" />
            </IconButton>
            {data.wallet}
          </div>
        </TableCell>
        <TableCell>
          <Button
            style={{
              backgroundColor: "#464446",
              color: "#fff",
              marginRight: 5,
            }}
            onClick={() => navigate("/deposit", { state: data })}
            size="small"
          >
            D
          </Button>
          <Button
            style={{
              backgroundColor: "#464446",
              color: "#fff",
              marginRight: 5,
            }}
            onClick={() => navigate("/withdraw", { state: data })}
            size="small"
          >
            W
          </Button>
          <Button
            style={{
              backgroundColor: "#464446",
              color: "#fff",
              marginRight: 5,
            }}
            size="small"
          >
            L
          </Button>
          <Button
            style={{
              backgroundColor: "#464446",
              color: "#fff",
              marginRight: 5,
            }}
            onClick={(e) => updatemode(e, data)}
            size="small"
          >
            P
          </Button>
        </TableCell>
      </TableRow>
    </>
  );
};
export default AdminCard;
