import React from "react";
import { List } from "@mui/material";
import BidCards from "../Cards/BidCards";

const UserBid = ({ list }) => (
  <List
    sx={{
      width: "100%",
      bgcolor: "background.paper",
      overflow: "auto",
      maxHeight: 300,
    }}
  >
    {list && list.map((item) => <BidCards data={item} key={item._id} />)}
  </List>
);
export default UserBid;
