import React, { useEffect, useState } from "react";
import { Card, Grid, CardContent, Typography } from "@mui/material";
// Core components
import Header from "../../header";
import Wrapper from "../../wrapper";
import Loaders from "../../loader";
//api
import DataService from "../../services/requestApi";
import UserCards from "../../Cards/UserCards";
import { useLocation } from "react-router-dom";
const AllUsers = (props) => {
  const [data, setData] = useState([]);
  const [loading, setLoading] = useState(false);
  let { state } = useLocation();
  useEffect(() => {
    const getAllSuperAdmin = async () => {
      setLoading(true);
      try {
        const res = await DataService.SuperAdminFetchAlladmin(state._id);
        setData(res.data.data);
        setLoading(false);
      } catch (e) {
        console.log(e);
      }
    };
    getAllSuperAdmin();
    return () => {
      getAllSuperAdmin();
    };
  }, [state]);

  return (
    <>
      <Header />
      <Loaders open={loading} />
      <Wrapper>
        <Grid container justifyContent="center" alignItems={"center"}>
          <Grid item xl={11} lg={11} xs={12}>
            <Card className="mb3">
              <CardContent>
                <Typography variant="h5">All User of {state.name}</Typography>
              </CardContent>
            </Card>
            <Card className="mb3">
              <UserCards data={data} />
            </Card>
          </Grid>
        </Grid>
      </Wrapper>
    </>
  );
};
export default AllUsers;
