import React from "react";
import { Button, IconButton, TableCell, TableRow } from "@mui/material";
import { useNavigate } from "react-router-dom";
import CustomTable from "../CustomTable";
// Icons
import EditIcon from "@mui/icons-material/Edit";
import ChairIcon from "@mui/icons-material/Chair";
import FactCheckIcon from "@mui/icons-material/FactCheck";
const AllParty = ({ data, openModal }) => {
  const tableHead = [
    "Sr.no",
    "State",
    "Part Name",
    "No. of Seats",
    "Edit",
    "All Tickets",
    "Declare Results",
  ];
  let navigate = useNavigate();

  const Navigate = (e, data) => {
    navigate("/editparty", {
      state: data,
    });
  };

  const NavigateTkt = (e, data) => {
    navigate("/tickets", {
      state: data,
    });
  };
  return (
    <>
      <CustomTable tablehead={tableHead}>
        {data &&
          data.map((item, index) => (
            <TableRow key={item._id}>
              <TableCell>{index + 1}</TableCell>
              <TableCell>{item?.state_id?.name}</TableCell>
              <TableCell>{item.name}</TableCell>
              <TableCell>{item.seats}</TableCell>

              <TableCell>
                <IconButton
                  onClick={(e) => Navigate(e, item)}
                  disabled={item.result_declare_status}
                >
                  <EditIcon
                    fontSize="small"
                    color={item.result_declare_status ? "default" : "primary"}
                  />
                </IconButton>
              </TableCell>

              <TableCell>
                <Button
                  variant="outlined"
                  onClick={(e) => NavigateTkt(e, item)}
                  color="primary"
                  startIcon={<FactCheckIcon fontSize="small" color="primary" />}
                >
                  See Tickets
                </Button>
              </TableCell>

              <TableCell>
                {item.result_declare_status ? (
                  <Button
                    style={{ padding: 0 }}
                    color="success"
                    startIcon={<ChairIcon fontSize="small" />}
                  >
                    Seats {item?.winning_seats} Declared
                  </Button>
                ) : (
                  <Button
                    variant="outlined"
                    color="error"
                    onClick={(e) => openModal(e, item._id)}
                    startIcon={<ChairIcon fontSize="small" color="error" />}
                  >
                    Declare Result
                  </Button>
                )}
              </TableCell>
            </TableRow>
          ))}
      </CustomTable>
    </>
  );
};
export default AllParty;
