import React from "react";
// Core components
import CustomTable from "../CustomTable";
import AdminCard from "../Cards/AdminCard";
const AllAdmin = ({ data, updatemode }) => {
  const tableHead = ["Admin Name", "Credit Referance", "User Name", "Users", "Status", "Bid Status", "Wallet", "Action"];
  return (
    <>
      <br />
      <CustomTable tablehead={tableHead}>{data && data.map((item, index) => <AdminCard data={item} sr={index} key={item._id} updatemode={updatemode} />)}</CustomTable>
    </>
  );
};
export default AllAdmin;
