import React, { useEffect, useState } from "react";
import {
  Card,
  Grid,
  CardContent,
  Typography,
  Button,
  TextField,
  Snackbar,
  Alert,
} from "@mui/material";

// core components
import Header from "../../header";
import Wrapper from "../../wrapper";
import AllAdmin from "../../Admin";
import Loaders from "../../loader";
import CustomModal from "../../CustomModal";

//api
import DataService from "../../services/requestApi";
import queryString from "query-string";
import UserCards from "../../Cards/UserCards";

import { socket } from "../../services/socket";
const DashBoard = (props) => {
  const [Alladmin, setAllAdmin] = useState([]);
  const [Alluser, setAllUser] = useState([]);
  const [loading, setLoading] = useState(false);
  const [mounted, setMounted] = useState(true);
  const [names, setName] = useState("");
  const [code, setCode] = useState("");
  const [password, setPassword] = useState("");
  const [cpassword, setCPassword] = useState("");
  const [error, setError] = useState(false);
  const [errorMsg, setErrorMsg] = useState("");
  const [modal, setModal] = useState(false);
  const [updateStatus, setUpdateStatus] = useState(false);
  const [currentId, setCurrentId] = useState("");
  const [adminExists, setAdminExists] = useState(false);
  const [adminExistsMsg, setAdminExistsMsg] = useState("");

  useEffect(() => {
    const GetData = async () => {
      setLoading(true);
      try {
        const [alladmin, alluser] = await Promise.all([
          DataService.SuperAdminFetchAll(),
          DataService.SuperAdminFetchAllUser(),
        ]);
        if (mounted) {
          // setAdminData(ownerres.data);
          setAllAdmin(alladmin.data.data);
          setAllUser(alluser.data.data);
          setLoading(false);
        }
      } catch (e) {
        console.log(e);
      }
    };
    GetData();
    return () => {
      GetData();
      setMounted(false);
    };
  }, [mounted]);

  const ToggleModal = () => {
    setModal(!modal);
    if (!modal) {
      setName();
      setCurrentId();
      setUpdateStatus(false);
    }
  };
  const SubmitDetails = async (e) => {
    e.preventDefault();
    try {
      ToggleModal();
      const Data = {
        name: names,
        password,
        code,
      };
      const res = await DataService.AdminAdd(queryString.stringify(Data));

      if (res.data.status_code === 400) {
        setError(true);
        setErrorMsg(res.data.message);
      } else {
        setName();
        setCode();
        setPassword();
        setCPassword();
        setMounted(true);
      }
    } catch (e) {
      console.log(e);
    }
  };

  const UpdateMode = (e, data) => {
    ToggleModal();
    setUpdateStatus(true);
    setName(data.name);
    setCurrentId(data._id);
  };
  const UpdateDetails = async (e) => {
    e.preventDefault();
    try {
      ToggleModal();
      var Data = {
        name: names,
      };
      if (password.length > 6) {
        Data.password = password;
      } else {
        ToggleModal();
        setError(true);
        setErrorMsg("Please enter atleast 6 characters");
      }
      const res = await DataService.AdminUpdate(currentId, queryString.stringify(Data));
      if (res.data.status_code === 400) {
        setError(true);
        setErrorMsg(res.data.message);
      } else {
        setMounted(true);
        socket.on("connect", () => {
          const DATA = {
            admin_id: currentId,
            socket_id: socket.id,
          };
          socket.emit("adminData", DATA, (error) => {
            if (error) {
              alert(error);
            }
          });
        });
      }
      ToggleModal();
    } catch (e) {
      console.log(e);
    }
  };

  const UserStatus = async (e, type, data) => {
    try {
      var Data = {};
      if (type === "status") {
        Data.status = !data.status;
      } else {
        Data.bid_status = !data.bid_status;
      }
      const res = await DataService.SuperAdminUpdateUser(data._id, queryString.stringify(Data));
      if (res.data.message === "Success !") {
        setMounted(true);
        if (type === "status") {
          socket.on("connect", () => {
            const DATA = {
              user_id: data._id,
              socket_id: socket.id,
            };
            socket.emit("userData", DATA, (error) => {
              if (error) {
                alert(error);
              }
            });
          });
        }
      }
    } catch (e) {
      console.log(e);
    }
  };

  useEffect(() => {
    const CheckUserExists = async (e) => {
      try {
        const Data = {
          code,
        };
        if (!updateStatus) {
          if (code && code.length > 4) {
            const res = await DataService.CheckAdminExists(queryString.stringify(Data));
            console.log(res.data);
            setAdminExists(true);
            setAdminExistsMsg(res.data.message);
          } else {
            setAdminExists(false);
          }
        }
      } catch (e) {
        console.log(e);
      }
    };
    CheckUserExists();
  }, [code]);
  return (
    <>
      <Header />
      <Loaders open={loading} />
      {error && (
        <Snackbar open={error} autoHideDuration={1000}>
          <Alert severity="error">{errorMsg}</Alert>
        </Snackbar>
      )}
      <Wrapper>
        <Grid container justifyContent="center" alignItems={"center"}>
          <Grid item xl={11} lg={11} xs={12}>
            <div className="">
              <CardContent>
                <Grid container justifyContent="center" alignItems={"center"}>
                  <Grid item xl={6} lg={6} xs={6}>
                    <Typography variant="h5">All Admin </Typography>
                  </Grid>
                  <Grid item xl={6} lg={6} xs={6}>
                    <div className="wallet" style={{ textAlign: "right", display: "block" }}>
                      <Button variant="outlined" onClick={ToggleModal} size="small">
                        Add admin
                      </Button>
                      <br />
                    </div>
                  </Grid>
                </Grid>
                <AllAdmin data={Alladmin} updatemode={UpdateMode} />
              </CardContent>
            </div>
            <div className="mb3">
              <CardContent>
                <Typography variant="h5">All User</Typography>
                <br />
                <UserCards data={Alluser} UserStatus={UserStatus} />
              </CardContent>
            </div>
          </Grid>
        </Grid>
      </Wrapper>
      <CustomModal
        open={modal}
        handleClose={ToggleModal}
        title={updateStatus ? "Edit Admin" : "Add Admin"}
      >
        <Wrapper>
          <Grid container justifyContent="center" alignItems={"center"}>
            <Grid item xl={5} lg={4} xs={12}>
              <Card className="mb3">
                <CardContent>
                  <form autoComplete="off" onSubmit={updateStatus ? UpdateDetails : SubmitDetails}>
                    <TextField
                      className="w-100 mb3"
                      id="name"
                      label="Enter Name"
                      type="text"
                      variant="outlined"
                      value={names}
                      focused={!updateStatus}
                      onChange={(e) => setName(e.target.value)}
                      required
                    />
                    {!updateStatus && (
                      <>
                        <TextField
                          className="w-100 mb3"
                          id="username"
                          label="Enter Username"
                          type="text"
                          variant="outlined"
                          value={code}
                          autoComplete="new-username"
                          onChange={(e) => setCode(e.target.value)}
                          required
                        />
                        {adminExists && <p style={{ marginBottom: 15 }}>{adminExistsMsg} !</p>}
                      </>
                    )}
                    <TextField
                      className="w-100 mb3"
                      id="password"
                      label="Enter Password"
                      type="password"
                      variant="outlined"
                      value={password}
                      autoComplete="new-password"
                      required
                      onChange={(e) => setPassword(e.target.value)}
                    />
                    <TextField
                      className="w-100 mb3"
                      id="cpassword"
                      label="Confirm Password"
                      type="password"
                      variant="outlined"
                      value={cpassword}
                      required
                      autoComplete="cnew-password"
                      onChange={(e) => setCPassword(e.target.value)}
                    />
                    {cpassword === password ? null : (
                      <p style={{ color: "red", marginBottom: 15 }}>
                        Please Enter Correct Password
                      </p>
                    )}
                    {!updateStatus ? (
                      <Button
                        variant="contained"
                        className="w-100"
                        disabled={cpassword === password ? false : true}
                        type="submit"
                      >
                        Submit
                      </Button>
                    ) : (
                      <Button
                        variant="contained"
                        className="w-100"
                        disabled={cpassword === password ? false : true}
                        type="submit"
                      >
                        Update
                      </Button>
                    )}
                  </form>
                </CardContent>
              </Card>
            </Grid>
          </Grid>
        </Wrapper>
      </CustomModal>
    </>
  );
};
export default DashBoard;
