import React, { useEffect } from "react";
import { useNavigate } from "react-router-dom";
const Logout = (props) => {
  const navigate = useNavigate();
  useEffect(() => {
    sessionStorage.removeItem("Authtoken");
    navigate("/login");
  }, []);
  return (
    <>
      <h1>Logout</h1>
    </>
  );
};
export default Logout;
